@import url(https://fonts.googleapis.com/css2?family=Fraunces:opsz,wght@9..144,700&family=Montserrat:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,*::after,*::before{margin:0;padding:0;box-sizing:border-box}body{background-color:#f2ebe3}.App{display:grid;place-items:center;height:100vh}.App .wrapper{max-width:43rem;display:flex;justify-content:center;align-items:center}.App .wrapper img{width:335px;border-radius:10px 0 0 10px}.App .wrapper .card{border-radius:0 10px 10px 0;background-color:#fff;font-family:"Montserrat",sans-serif;padding:3rem;word-wrap:break-word}.App .wrapper .card h3{margin:10px 0;text-transform:uppercase;padding-bottom:10px;font-weight:500;color:#1c232b}.App .wrapper .card h1{margin:10px 0;font-family:"Fraunces",serif;max-width:7em}.App .wrapper .card p{margin:10px 0;width:90%;font-size:14px;color:#6c7289;line-height:1.5}.App .wrapper .card ul{margin:20px 0;display:flex;align-items:center}.App .wrapper .card ul li{list-style:none}.App .wrapper .card ul .first{margin-right:auto;font-size:30px;color:#3c8067;font-family:"Fraunces",serif}.App .wrapper .card ul .second{margin-right:auto;text-decoration:line-through;color:#6c7289}.App .wrapper .card button{outline:none;border:none;background-color:#3c8067;width:100%;font-family:"Montserrat",sans-serif;font-size:14px;color:#fff;padding:15px 0px;border-radius:10px;margin:10px 0;cursor:pointer}.App .wrapper .card button:active{background-color:#1c232b}.App .wrapper .card button svg{margin-right:5px}@media(max-width: 700px){.App .wrapper{flex-direction:column;margin:20px auto}.App .wrapper img{width:350px;border-radius:10px 10px 0 0}.App .wrapper .card{-webkit-transform:translateY(-200px);transform:translateY(-200px);border-radius:0 0 10px 10px;max-width:350px;padding:1.5rem}.App .wrapper .card h3{margin:0;padding:0}.App .wrapper .card h1{margin:0;word-wrap:normal;max-width:20rem}.App .wrapper .card p{width:100%}.App .wrapper .card ul .first{margin-right:20px}.App .wrapper .card ul .second{margin-right:auto}}/*# sourceMappingURL=style.css.map */
